import styled from "styled-components";
import {MdArrowForward, MdKeyboardArrowRight} from 'react-icons/md';
import {Link as LinkS} from 'react-scroll';
export const HeroContainer=styled.div`
    background: #0c0c0c;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 800px;
    position: relative;
    z-index: 1;
    
    :before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(180deg,rgba(0,0,0,0.2) 0%,rgba(0,0,0,0.6) 100%),
        linear-gradient(180deg,rgba(0,0,0,0.2) 0%,transparent 100%);
        z-index:2;
    }
`;
export const HeroBg=styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
`;

export const VideoBg=styled.video`
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    background: #232a34;
`;

export const HeroContent=styled.div`
    z-index: 3;
    max-width: 1200px;
    position: absolute;
    padding: 8px 38px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;    
`;

export const HeroH1=styled.h1`
    color: #F7F6F2;
    font-size: 45px;
    text-align: center;

    @media screen and (max-width: 768px){
        font-size: 40px;
    }
    @media screen and (max-width: 480px){
      
        font-size: 32px;
    }
`;

export const HeroP=styled.p`
    margin-top: 24px;
    color: #F7F6F2;
    font-size: 30px;
    text-align: center;
    align-items: center;
    max-width: 600px;

    @media screen and (max-width: 768px){
        font-size: 24px;
    }
    @media screen and (max-width: 480px){
      
        font-size: 18px;
    }
`;

export const HeroBtnWrapper=styled.div`
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

export const ArrorForward=styled(MdArrowForward)`
    margin-left: 8px;
    font-size: 20px;
`;

export const ArrowRight=styled(MdKeyboardArrowRight)`
    margin-left: 8px;
    font-size: 20px;
`;
export const NavLinks=styled(LinkS)`
display: flex;
align-items: center;
border-radius:50px;
background: #79B4B7;
white-space: nowrap;
padding:10px 22px;
color: #F7F6F2;
font-size: 20px;
outline: none;
border: none;
cursor: pointer;
transition: all 0.2s ease-in-out;
text-decoration: none;
height: max-content;
margin-top: 20px;

&:hover{
    transition: all 0.2s ease-in-out;
    background: #F7F6F2;
    color: #79B4B7;
}
`;