import styled from 'styled-components';
import {Link as LinkR} from 'react-router-dom';
import {Link as LinkS} from 'react-scroll';
export const Nav=styled.nav`
    background: ${({scrollNav})=>(scrollNav?'#11324d':'transparent')};
    
    height: 80px;
    margin-top: -80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.25rem;
    position: sticky;
    top: 0;
    z-index: 10;

    @media screen and (max-width: 960px){
        transition: 0.8s all ease;
    }
`;
export const NavbarContainer=styled.div`
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    width: 100%;
    padding: 0 24px;
    max-width: 1100px;
`;
export const Navlogo=styled(LinkR)`
 color: #F7F6F2;
 justify-self: flex-start;
 cursor: pointer;
 font-size: 2rem;
 display: flex;
 align-items: center;
 margin-left: 10px;
 font-weight: bold;
 text-decoration: none;
 &:hover{
     color: #79B4B7;
 }
`;
export const MobileIcon=styled.div`
display: none;
@media screen and (max-width: 768px){
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%,60%);
    font-size: 1.9rem;
    cursor: pointer;
    color: #F7F6F2;
    
}
`;
export const NavMenu=styled.ul `
display:flex;
align-items: center;
list-style: none;
text-align: center;
margin-right: -22px;

@media screen and (max-width: 760px){
    display: none;
}
`;
export const NavItem=styled.li`
height: 80px;
`;

export const NavLinks=styled(LinkS)`
color: #F7F6F2;
display: flex;
align-items:center;
text-decoration: none;
padding: 0 1rem;
height: 100%;
cursor: pointer;

&.active{
    border-bottom: 3px solid #79B4B7;
}
&:hover{
    color: #79B4B7;
}
`;
export const NavBtn=styled.nav`
display:flex;
color: #F7F6F2;
align-items:centre;
@media screen and (max-width: 768px){
    display: none;
}
`;
export const NavBtnLink=styled(LinkR)`
border-radius:50px;
background: #79B4B7;
white-space: nowrap;
padding:10px 22px;
color: #F7F6F2;
font-size: 20px;
outline: none;
border: none;
cursor: pointer;
transition: all 0.2s ease-in-out;
text-decoration: none;
height: max-content;
margin-top: 20px;

&:hover{
    transition: all 0.2s ease-in-out;
    background: #F7F6F2;
    color: #79B4B7;
}
`;