import React from 'react';
import { Button } from '../ButtonElement';
import { Column2, ImgWrap } from './InfoElements';
import { InfoContainer, InfoWrapper, InfoRow, Column1, TextWrapper, TopLine, Heading, Subtitle, BtnWrap, Img} from './InfoElements';
const InfoSection = ({targ,to,lightBg,id,imgStart,topLine,lightText,darkText,buttonLabel,headLine,description,img,alt,primary,dark,dark2}) => {
    return (
        <>
        <InfoContainer lightBg={lightBg} id={id}>
            <InfoWrapper>
                <InfoRow imgStart={imgStart}>
                    <Column1>
                        <TextWrapper>
                            {/* <TopLine>{topLine}</TopLine> */}
                            <Heading lightText={lightText}>{topLine}</Heading>
                            <Subtitle darkText={darkText}>{description}</Subtitle>
                            <BtnWrap>
                                <Button to={to}
                                target={targ}
                                rel='noopener noreferrer' 
                                smooth={true}
                                duration={true}
                                spy={true}
                                exact="true"
                                offset={-80}
                                primary={primary? 1:0}
                                dark={dark?1:0}
                                dark2={dark2?0:1}
                                >{buttonLabel}</Button>
                            </BtnWrap>
                        </TextWrapper>
                    </Column1>
                    <Column2>
                        <ImgWrap>
                            <Img src={img} alt={alt}/>
                        </ImgWrap>
                    </Column2>
                </InfoRow>
            </InfoWrapper>
        </InfoContainer>
        </>
    )
}

export default InfoSection
